import AbstractView from '../framework/view/abstract-view.js';

/**
 * @description      
 * @param {Object} props -   
 * @returns {string}
 */
function createSortItemTemplate({ sortType, label, isChecked, isDisabled }) {

  return (
    `<div class="trip-sort__item  trip-sort__item--${sortType}">
      <input
        id="sort-${sortType}"
        class="trip-sort__input  visually-hidden"
        type="radio"
        name="trip-sort"
        value="sort-${sortType}"
        ${isChecked ? 'checked' : ''}
        ${isDisabled ? 'disabled' : ''}
      >
      <label class="trip-sort__btn" for="sort-${sortType}">${label}</label>
    </div>`

  );
}

/**
 * @description      
 */
export default class SortItemView extends AbstractView {
  /**
   * @description  
   * @type {Object}
   */
  #props = null;

  /**
   * @param {Object} props -  
   */
  constructor(props) {
    super();
    this.#props = props;
  }

  /**
   * @description    
   * @returns {string}
   */
  get template() {
    return createSortItemTemplate(this.#props);
  }
}
