import AbstractView from '../framework/view/abstract-view.js';

/**
 * @description      
 * @param {Object} filter -  
 * @returns {string}
 */
function createFilterItemTemplate(filter) {
  const { id, value, label, isDisabled, isChecked } = filter;

  return (
    `<div class="trip-filters__filter">
      <input
        id="filter-${id}"
        class="trip-filters__filter-input  visually-hidden"
        type="radio"
        name="trip-filter"
        value="${value}"
        ${isChecked ? 'checked' : ''}
        ${isDisabled ? 'disabled' : ''}
      >
       <label class="trip-filters__filter-label" for="filter-${id}">${label}</label>
    </div>`
  );
}

/**
 * @description      
 */
export default class FilterItemView extends AbstractView {
  /**
   * @description  
   * @type {Object}
   */
  #filter = null;

  /**
   * @param {Object} args -  
   * @param {Object} args.filter -  
   */
  constructor({ filter }) {
    super();
    this.#filter = filter;
  }

  /**
   * @description    
   * @returns {string}
   */
  get template() {
    return createFilterItemTemplate(this.#filter);
  }
}
