import { offers } from '../mock/offers-mock.js';

/**
 * @description      
 */
export default class OffersModel {
  /**
   * @description  
   * @type {Array}
   */
  #offers = offers;

  /**
   * @description     
   * @returns {Array}
   */
  get offers() {
    return this.#offers;
  }

  /**
   * @description        
   * @returns {Array}
   */
  #getAllOffers() {
    return this.#offers.flatMap((item) => item.offers);
  }

  /**
   * @description       
   * @param {string} type -  
   * @returns {Array}
   */
  getOffersByType(type = '') {
    return this.#offers.find((item) => item.type === type)?.offers || [];
  }

  /**
   * @description       ID
   * @param {Array} ids -  ID 
   * @returns {Array}
   */
  getOffersByIds(ids = []) {
    return this.#getAllOffers().filter((offer) => ids.includes(offer.id));
  }

  /**
   * @description       
   * @returns {Array}
   */
  getPointTypes() {
    return this.#offers.map((item) => item.type);
  }
}
